import React, { useState }         from 'react'
import { useTranslation }          from 'react-i18next'
import { Link }                    from 'react-router-dom'
import { FaBars, FaTimes, FaUser } from 'react-icons/fa'
import { useSelector }             from 'react-redux'
import NavAvatarUser               from './NavAvatarUser'
import Menu                        from '@material-ui/core/Menu'
import MenuItem                    from '@material-ui/core/MenuItem'

const Nav = ({ bgColor = '', typoColor = '' }) => {
  const [clicked, setClicked] = useState(false)
  const { t }                 = useTranslation()
  const userContext           = useSelector(state => state.userReducers)
  const navItems              = [
    {
      label: t('home'),
      url  : '/accueil',
      cName: 'nav-link'
    },
    {
      label: t('organization'),
      cName: 'nav-link',
      child: [
        {
          label: 'Mariage',
          url  : '/organisation/mariage',
          cName: 'dropdown-link'
        },
        {
          label: 'Crmonie Laque',
          url  : '/organisation/ceremonie-laique',
          cName: 'dropdown-link'
        },
        {
          label: 'EVJG / EVJF',
          url  : '/organisation/evj',
          cName: 'dropdown-link'
        },
        {
          label: 'Baby shower',
          url  : '/baby-shower',
          cName: 'dropdown-link'
        },
        {
          label: 'Autres vnements',
          url  : '/organisation/autres-evenements',
          cName: 'dropdown-link'
        },
      ]
    },
    {
      label: t('floralDesign'),
      url  : '/conception-floral',
      cName: 'nav-link'
    },
    {
      label: t('workshop'),
      url  : '/mes-ateliers',
      cName: 'nav-link'
    },
    {
      label: t('gallery'),
      url  : '/galerie',
      cName: 'nav-link'
    },
    {
      label: t('about'),
      url  : '/a-propos',
      cName: 'nav-link'
    },
    {
      label: t('contact'),
      url  : '/contact',
      cName: 'nav-link'
    },
    {
      label: t('opinion'),
      url  : '/avis',
      cName: 'nav-link'
    }
  ]
  
  const [anchorEl, setAnchorEl] = useState(null)
  
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget)
  }
  
  const handleClose = () => {
    setAnchorEl(null)
  }
  
  return (
    <nav className="navbar" style={{ background: bgColor, color: typoColor }}>
      <div className="menu-mobile" onClick={() => setClicked(!clicked)}>
        {!clicked ? <FaBars/> : <FaTimes/>}
      </div>
      <div className={clicked ? 'nav-container active' : 'nav-container'}>
        <div className="nav-mobile-logo">
          <img src="/assets/logo.png" alt="logo de Ct Campagne"/>
        </div>
        <ul className="nav-list">
          {navItems.map((link, index) => (<>
              {!link.child ?
               <li key={index} className="nav-item">
                 <Link to={link.url} className="nav-link" onClick={() => setClicked(!clicked)}>{link.label}</Link>
               </li> : <>
                 <li className="nav-item" onClick={handleClick} style={{cursor: 'pointer'}}>Organisation</li>
                 <Menu
                   id="simple-menu"
                   anchorEl={anchorEl}
                   keepMounted
                   open={Boolean(anchorEl)}
                   onClose={handleClose}
                 >
                   {link.child.map((l, index) => (
                     <>
                       <MenuItem onClick={handleClose}>
                         <Link to={l.url}>{l.label}</Link>
                       </MenuItem>
                     </>
                   ))}
                 </Menu>
               </>
              }
            </>
          ))}
        </ul>
      </div>
      <div className={clicked ? 'overlay-nav active' : 'overlay-nav'} onClick={() => setClicked(!clicked)}/>
      <div className="nav-user">
        {!userContext.isLogged ?
         <Link to="/connexion">{t('login')}<span><FaUser/></span></Link>
                               : <>
           <div>
         <span>
         <NavAvatarUser label={`${userContext.userInfo.firstName} ${userContext.userInfo.lastName}`}
                        userInfo={userContext.userInfo}/>
            </span>
           </div>
         </>
        }
      </div>
    </nav>
  )
}

export default Nav
